const validate_rules = {

    //
    'require': {
        "messages": function(value, element,params,validator){
            return ""
        },
        "reg": (value, element,params,validator)=>{
            return value.trim() != '';
        }
    },
    //
    "range": {
        "messages": function(value, element,params,validator){
            return ""+ params[0] +""+ params[1] +""
        },
        "reg": function (value, element,params,validator) {
            return Number(value)>=params[0] && Number(value)<=params[1];
        }
    },

    //
    "isIntGt0": {
        "messages": function(value, element,params,validator){
            return "0"
        },
        "reg": function (value, element,params,validator) {
            value = value.trim();
            return ((/^\d+$/.test(value)) && value>0);
        }
    },
    //
    "equal": {
        "messages": function(value, element,params,validator){
            return ""+ params.chinese +""
        },
        "reg": function (value, element,params,validator) {
            var value2 = validator.getNativeElementByName(params.name).value.trim();
            if(value2){
                value = value.trim();
                return value == value2;
            }else{
                return true;
            }

        }
    },
    //
    "iphone": {
        "messages": function(value, element,params,validator){
            return ""
        },
        "reg": function (value, element,params,validator) {
            return /^1[34578]\d{9}$/.test(value);
        }
    },
    //
    "password": {
        "messages": function(value, element,params,validator){
            return "6~18"
        },
        "reg": function (value, element,params,validator) {
            return /^(?![^a-zA-Z]+$)(?!\D+$)/.test(value);
        }
    }
}

export default validate_rules;


